	export default {
	    props: {
            catShow: {
                type: Boolean,
	            default: function() {
	                return true;
	            }
            },
	        list: {
	            type: Array,
		        default: function() {
		            return [];
            }
          },
          topicList: {
              type: Array,
              default: function () {
                  return []
              }
          },
          //标签颜色
          tagColor: {
              type: String
          },
          //选中颜色
          catSelectedColor: {
              type: String
          },
          //未选中颜色
          catUnselectedColor: {
              type: String
          },
          //背景色
          catBgColor: {
              type: String
          },
      },
	    data() {
	        return {
                tabCurrentIndex: 0,
                touchStartX: 0,
                touchStartY: 0,
                time: 0,
                interval: '',
                touchMoveX: 0,
                touchMoveY: 0,
            }
	    },
		methods: {
            tabClick(index) {
                this.tabCurrentIndex = index;
            },
            touchStart(e) {
                this.touchStartX = e.touches[0].pageX;
                this.touchStartY = e.touches[0].pageY; // 获取触摸时的原点
                // 使用js计时器记录时间
                this.interval = setInterval(() => {
                    this.time++;
                }, 100);
            },
            touchmove(e) {
                this.touchMoveX = e.touches[0].pageX;
                this.touchMoveY = e.touches[0].pageY;
            },
            touchend() {
                let moveX = this.touchMoveX - this.touchStartX;
                let moveY = this.touchMoveY - this.touchStartY;
                if (Math.sign(moveX) === -1) {
                    moveX = moveX * -1;
                }
                if (Math.sign(moveY) === -1) {
                    moveY = moveY * -1;
                }
                if(moveX >= moveY) {// 左右
                    // 向左滑动
                    if (this.touchMoveX - this.touchStartX <= -20 && this.time < 10) {
                        if (this.tabCurrentIndex === 0 ) {
                            this.tabCurrentIndex = this.list.length -1;
                        } else {
                            this.tabCurrentIndex -= 1;
                        }
                    }
                    // 向右滑动
                    if (this.touchMoveX - this.touchStartX >= 20 && this.time < 10) {
                        if (this.tabCurrentIndex === this.list.length -1) {
                            this.tabCurrentIndex = 0;
                        } else {
                            this.tabCurrentIndex += 1;
                        }
                    }
                }
                clearInterval(this.interval); // 清除setInterval
                this.time = 0;
            }
		}
	}
